import {BaseElement, BASIS, Label, OLabelInit} from "back/commons/basis";
import {MxFormElement, OFormElementInit} from "lib/commons/forms";
import {REG} from "lib/commons/registry";
import {DOM, JSX} from "lib/commons/xml/dom";
import {Button} from "back/commons/widgets/buttons";
import {DOMSH} from "lib/commons/xml/domsh";
import {Grid, GridSmall, OGridSmallInit} from "back/commons/widgets/grid-tags";
import {CellBuilderIconLabel, CellBuilderLabel, GridDataHolderJsonArray, GridDataRowJson} from "back/commons/widgets/grid-libs";
import {EGridDropPos, GridColDef, IGrid, IGridDataRow, IGridLineDrawer} from "back/commons/widgets/grid-core";
import {POPUP} from "back/commons/widgets/popups";

export type IOrderedSetEntry = {
	label: string;
	icon?: string;
	iconFilter?: string;
	description?: string,
	/* Entrée inconnue */
	notAvailable?: boolean,
};

export interface OOrderedSetInit extends OFormElementInit {
	/** Valeur initiale du champ */
	value?: string[];
	/** Ensemble des valeurs disponibles */
	dataSet: Dict<IOrderedSetEntry>;
	/** Modalité de visualisation des valeurys courantes. horizontal par défaut */
	previewMode?: 'asLines' | 'horizontal'
	/** Message affiché lorsqu'aucune valeur n'est sélectionné */
	emptySelectionMsg?: string;
	/** Label affiché au dessus de la grille des valeurs sélectionnées */
	selectionLabel: string | OLabelInit;
	/** Label affiché au dessus de la grille des valeurs disponible */
	availableLabel: string | OLabelInit;
	/** LineDrawer de la grille des valeurs sélectionnées */
	selectionGridInit?: OGridSmallInit;
	/** LineDrawer de la grille des valeurs disponible */
	availableGridInit?: OGridSmallInit;
	/**  Fction évaluant la validité de la sélection
	 * @param selectedEntries
	 * @return true si valide; false|string si non valide
	 */
	checkValidity?: (this: InputOrderedSetPanel, selectedEntries?: string[]) => boolean | string
}

type IOrderedSetData = { value: string } & IOrderedSetEntry;

/**
 * Input de sélection d'un ensemble ordonné de valeur dans un ensemble donné (le dataSet)
 */
export class InputOrderedSet extends MxFormElement(BaseElement) implements IGridLineDrawer {
	protected _leftBtn: Button;
	protected _rightBtn: Button;
	protected _upBtn: Button;
	protected _downBtn: Button;

	protected _params: OOrderedSetInit;

	/** Grille des valeurs sélectionnées */
	protected _selectionGrid: GridSmall;
	/** Grille des valeurs disponibles */
	protected _availableGrid: GridSmall;

	/** DataHolder des valeurs sélectionnées */
	protected _selection = new GridDataHolderJsonArray<IOrderedSetData>();

	/** DataHolder des valeurs disponibles */
	protected _available = new GridDataHolderJsonArray<IOrderedSetData>();

	/** Flag permettant de détecter l'origine du drag */
	protected dragFrom: null | GridSmall;

	protected _dataSet: Dict<IOrderedSetEntry>;

	get value(): string[] {
		return this._selection.getDatas().map((data) => data.value);
	}

	set value(vals: string[]) {
		const selection: IOrderedSetData[] = [];
		const available: IOrderedSetData[] = [];

		if (vals) for (const val of vals) {
			if (val in this._dataSet) selection.push(Object.assign({value: val}, this._dataSet[val]));
			else selection.push({value: val, label: val, notAvailable: true});
		}

		for (const entryKey in this._dataSet) {
			if (!vals || !vals.includes(entryKey)) available.push(Object.assign({value: entryKey}, this._dataSet[entryKey]));
		}

		this._selection.setDatas(selection);
		this._available.setDatas(available);
	}

	get dataSet(): Dict<IOrderedSetEntry> {
		return this._dataSet;
	}

	set dataSet(dataSet: Dict<IOrderedSetEntry>) {
		const val = this.value;
		const availables = [];
		for (const entryKey in dataSet) {
			const data = Object.assign({value: entryKey}, dataSet[entryKey]);
			availables.push(data);
		}
		this._available.setDatas(availables);
		this._selection.setDatas([]);
		this._dataSet = dataSet;
		this.value = val;
	}

	formDisabledCallback(disabled: boolean) {
		/* Désactivation des grid par stylage : ajout d'un attribut */
		DOM.setAttrBool(this._selectionGrid, 'disabled', disabled);
		DOM.setAttrBool(this._availableGrid, 'disabled', disabled);
		this._leftBtn.disabled = this._rightBtn.disabled = this._upBtn.disabled = this._downBtn.disabled = disabled;
	}

	protected _initialize(init: OOrderedSetInit) {
		this._params = init;
		this.dataSet = init.dataSet;
		if (init.value) this.value = init.value;

		const reg = this.findReg(init);

		/* CellBuilder différent en fonction de la présence d'icône dans le dataSet */
		const hasIcon = Object.values(this._dataSet).some((data) => data.icon);
		const cellBuilder = hasIcon ? new CellBuilderIconLabel('label').setIconKey('icon').override("getIconFilter", (row: IGridDataRow) => {return row.getData("iconFilter")}) : new CellBuilderLabel('label');
		cellBuilder.setDescriptionFunc((row) => row.getData('description'));

		/* Initialisation de la grille des valeurs sélectionnées */
		this._selectionGrid = new GridSmall().initialize(Object.assign({
			selType: 'multi',
			columnDefs: [new GridColDef('value')
				.setFlex('1rem', 1, 1)
				.setCellBuilder(cellBuilder)
			],
			dataHolder: this._selection,
			hideHeaders: true,
			lineDrawer: this,
			skinOver: 'c-input-ordered-set/grid',
			emptyBody: (): Node => {
				if (typeof init.emptySelectionMsg == "string") {
					return <c-msg label={init.emptySelectionMsg} level="info"/>
				} else {
					return <c-msg label="Aucune sélection" level="info"/>
				}
			}
		}, init.selectionGridInit));
		this._selectionGrid.id = 'selectionGrid';
		this._selectionGrid.addEventListener("grid-select", this._onGridSelect);
		this._initDrag(this._selectionGrid);

		/* Initialisation de la grille des valeurs disponible qui diffère par un tri implicite */
		this._availableGrid = new GridSmall().initialize(Object.assign({
			selType: 'multi',
			columnDefs: [new GridColDef('value')
				.setDefaultSort(1, 'ascendant')
				.setFlex('1rem', 1, 1)
				.setCellBuilder(cellBuilder)
			],
			dataHolder: this._available,
			hideHeaders: true,
			lineDrawer: this,
			skinOver: 'c-input-ordered-set/grid',
		}, init.availableGridInit));
		this._availableGrid.id = 'availableGrid';
		this._availableGrid.addEventListener("grid-select", this._onGridSelect);
		this._initDrag(this._availableGrid);

		this._leftBtn = <c-button î={{title: "Ajouter", icon: "/@skin@/commons/icons/left.svg"}} onclick={this._onLeft}/> as Button;
		this._rightBtn = <c-button î={{title: "Enlever", icon: "/@skin@/commons/icons/right.svg"}} onclick={this._onRight}/> as Button;
		this._upBtn = <c-button î={{title: "Haut", icon: "/@skin@/commons/icons/up.svg"}} onclick={this._onUp}/> as Button;
		this._downBtn = <c-button î={{title: "Bas", icon: "/@skin@/commons/icons/down.svg"}} onclick={this._onDown}/> as Button;

		this._selectionGrid.uiEvents.on("rowDblclick", this._onDblClick);
		this._availableGrid.uiEvents.on("rowDblclick", this._onDblClick);

		const toolbar = <div id="toolbar">
			{this._leftBtn}
			{this._rightBtn}
			<div class="spacer"/>
			{this._upBtn}
			{this._downBtn}
		</div> as HTMLDivElement;

		let selectionLabel = null;
		if (init.selectionLabel) {
			if (typeof init.selectionLabel == "string") selectionLabel = new Label().initialize({label: init.selectionLabel});
			else selectionLabel = new Label().initialize(init.selectionLabel);
			selectionLabel.id = 'selectionLabel';
		}

		let availableLabel = null;
		if (init.availableLabel) {
			if (typeof init.availableLabel == "string") availableLabel = new Label().initialize({label: init.availableLabel});
			else availableLabel = new Label().initialize(init.availableLabel);
			availableLabel.id = 'availableLabel';
		}

		this._attach(this.localName, init, selectionLabel, this._selectionGrid, toolbar, availableLabel, this._availableGrid);
		reg.installSkin("scroll/small", this.shadowRoot);

		this._initializeForm(init);
		this._refresh();
	}

	protected _onLeft(this: Button) {
		if (this.disabled) return;
		const self = DOMSH.findHost<InputOrderedSet>(this);
		const count = self._selection.countRows();
		self._addToSelection(count);
	}

	protected _onRight(this: Button) {
		if (this.disabled) return;
		const self = DOMSH.findHost<InputOrderedSet>(this);
		self._removeFromSelection();
	}

	protected _onDblClick(this: void, row: IGridDataRow | null, ev: MouseEvent) {
		const grid = DOMSH.findHost(ev.target as HTMLElement);
		const self = DOMSH.findHost<InputOrderedSet>(grid);
		if (grid == self._availableGrid) self._addToSelection(self._selection.countRows());
		else if (grid == self._selectionGrid) self._removeFromSelection();
	}

	/** Déplacement d'une valeur disponible en valeur sélectionnée */
	protected _addToSelection(insertIndex: number) {
		const selected = this._available.getSelectedDatas();
		for (const sel of selected) this._available.deleteRowKey(sel);
		this._selection.updateDatas(insertIndex, 0, ...selected);
		this._selectionGrid.setSelectedRows([insertIndex, -(insertIndex + selected.length - 1)]);
		this._refresh();
		this._dispatchChange();
	}

	/** Suppresion d'une valeur sélectionné et réinsertion en valeur disponible */
	protected _removeFromSelection() {
		const selected = this._selection.getSelectedDatas();
		for (const sel of selected) this._selection.deleteRowKey(sel);
		// Utilisation de setDatas pour forcer le tri
		this._available.setDatas(this._available.getDatas().concat(selected));

		const available = this._available.getDatas();
		for (let i = 0; i < available.length; i++) {
			if (selected.includes(available[i])) this._availableGrid.addSelectedRows(i, i);
		}
		this._refresh();
		this._dispatchChange();
	}

	/** Déplacement vers le haut de l'ensemble de la sélection des valeurs sélectionnée */
	protected _onUp(this: Button) {
		if (this.disabled) return;
		const self = DOMSH.findHost<InputOrderedSet>(this);
		const selection = self._selection.getDatas();
		if (!selection.length || self._selectionGrid.isRowSelected(0)) return;
		const newSelection = [];
		const selectedOffsets = [];
		let i = 0;
		while (i < selection.length) {
			const data = selection[i];
			i++;
			while (self._selectionGrid.isRowSelected(i)) {
				newSelection.push(selection[i]);
				selectedOffsets.push(i - 1);
				i++;
			}
			newSelection.push(data);
		}
		self._selection.setDatas(newSelection);
		self._selectionGrid.setSelectedRows(selectedOffsets);
		self._dispatchChange();
	}

	/** Déplacement vers le bas de l'ensemble de la sélection des valeurs sélectionnée */
	protected _onDown(this: Button) {
		if (this.disabled) return;
		const self = DOMSH.findHost<InputOrderedSet>(this);
		const selection = self._selection.getDatas();
		if (!selection.length || self._selectionGrid.isRowSelected(selection.length - 1)) return;
		const newSelection = [];
		const selectedOffsets = [];
		let i = selection.length - 1;
		while (i >= 0) {
			const data = selection[i];
			i--;
			while (self._selectionGrid.isRowSelected(i)) {
				newSelection.unshift(selection[i]);
				selectedOffsets.push(i + 1);
				i--;
			}
			newSelection.unshift(data);
		}
		self._selection.setDatas(newSelection);
		self._selectionGrid.setSelectedRows(selectedOffsets);
		self._dispatchChange();
	}

	protected _onGridSelect(this: Grid) {
		DOMSH.findHost<InputOrderedSet>(this)._refresh();
	}

	protected _dispatchChange() {
		this.dispatchEvent(new CustomEvent("input", {bubbles: true, cancelable: false}));
		this.dispatchEvent(new CustomEvent("change", {bubbles: true, cancelable: false}));
	}

	protected _refresh() {
		this._leftBtn.disabled = !this._available.getSelectedDatas().length;
		const hasSelection = this._selection.getSelectedDatas().length;
		this._rightBtn.disabled = !hasSelection;
		this._upBtn.disabled = !hasSelection || this._selectionGrid.isRowSelected(0);
		this._downBtn.disabled = !hasSelection || this._selectionGrid.isRowSelected(this._selection.countRows() - 1);

		this._refreshValidity();
	}

	protected _refreshValidity(): void {
		const values = this.value;
		if (this.required && !this.value.length) this.setValidity({valueMissing: true}, "Veuillez sélectionner un élément.", this._selectionGrid);
		else if (this._params.checkValidity) {
			const result = this._params.checkValidity.call(this, values);
			if (result == true) {
				this.setValidity({});
			} else {
				if (typeof result == "string")
					this.setValidity({valueMissing: true}, result);
				else
					this.setValidity({valueMissing: true}, "Des groupes sélectionnés sont incompatibles");
			}
		} else this.setValidity({});
	}

	/** Initialisation du drag */
	protected _initDrag(grid: GridSmall) {
		grid.setRowDropMgr(this);
		grid.linesNode.setAttribute('draggable', 'true');
		grid.addEventListener('dragstart', function (this: GridSmall, ev) {
			const self = DOMSH.findHost<InputOrderedSet>(this);
			ev.dataTransfer.effectAllowed = 'move';
			ev.dataTransfer.setDragImage(this._getLine(this.getSelectedRow()), 0, 0);
			self.dragFrom = this;

		});
		grid.addEventListener('dragend', function (ev) {
			const self = DOMSH.findHost<InputOrderedSet>(this);
			self.dragFrom = null;
		});

	}

	onDragOverRow(ev: DragEvent, grid: IGrid, row: GridDataRowJson<IOrderedSetData>, line: HTMLElement): EGridDropPos | EGridDropPos {
		// Le drag n'est pas autorisé à l'intérieur des valeurs disponible
		if (!this.dragFrom || (grid == this._availableGrid && this.dragFrom == this._availableGrid)) {
			ev.dataTransfer.dropEffect = 'none';
			return EGridDropPos.none;
		}
		// Déplacement à l'intérieur de la sélection désactivé sur elle-même
		if (grid == this._selectionGrid && this.dragFrom == grid && row) {
			const offset = this._selection.getOffset(row.rowKey);
			if (grid.isRowSelected(offset)) {
				ev.dataTransfer.dropEffect = 'none';
				return EGridDropPos.none;
			}
		}
		ev.dataTransfer.dropEffect = 'move';
		return row ? EGridDropPos.before | EGridDropPos.after : EGridDropPos.after;
	}

	dropOnRow(ev: DragEvent, grid: IGrid, row: GridDataRowJson<IOrderedSetData>, line: HTMLElement, pos: EGridDropPos): void {
		if (grid == this._selectionGrid) {
			let insertIndex = row ? this._selection.getOffset(row.rowKey) : this._selection.countRows() - 1;
			if (pos === EGridDropPos.after) insertIndex++;
			if (this.dragFrom != grid) {
				this._addToSelection(insertIndex);
			} else {
				const selected = this._selection.getSelectedDatas();
				this._selection.setDatas(this._selection.getUnselectedDatas());
				insertIndex = Math.min(insertIndex, this._selection.countRows());
				this._selection.updateDatas(insertIndex, 0, ...selected);
				this._selectionGrid.setSelectedRows([insertIndex, -(insertIndex + selected.length - 1)]);
				this._dispatchChange();
			}
		} else if (grid == this._availableGrid && this.dragFrom != grid) {
			this._removeFromSelection();
		}
	}

	redrawLine(row: GridDataRowJson<any>, line: HTMLElement): void {
		if (row.rowDatas.notAvailable) line.classList.add('notAvailable');
	}
}


